<template>
  <div>
    <TopToolbar @searchString="searchString = $event"/>
    <v-container class="px-0">
      <card-list :itemList="searchedAromaFusions"/>
      <v-fab-transition>
        <v-btn class="mx-2 app-primary" fab small fixed right bottom
               :to="{ name: 'FlavourMerger'}">
          <v-icon color="white">mdi-plus</v-icon>
        </v-btn>
      </v-fab-transition>
    </v-container>
  </div>
</template>

<script>
// @ is an alias to /src
import CardList from '@/components/CardList.vue'
import TopToolbar from '@/components/TopToolbar'
import localStorageMixin from '@/mixins/localStorageMixin'

export default {
  name: 'Home',
  components: {
    CardList,
    TopToolbar
  },
  mixins: [localStorageMixin],
  data: () => ({
    searchString: ''
  }),
  computed: {
    searchedAromaFusions () {
      return this.searchString.length
              ? this.aromaFusions.filter(af => {
                let regex = new RegExp(this.searchString.toLowerCase())
                return af.name.toLowerCase().match(regex)
              })
              : this.aromaFusions
    }
  }
}
</script>
