<template>
    <v-row class=" pr-2">
        <v-btn color="black" icon @click="clickMagnify()" class="pl-4 ml-auto" :disabled="nothingToSearch">
            <v-icon>mdi-magnify</v-icon>
        </v-btn>
        <v-text-field v-if="!hideInput" v-model="searchString" @input="$emit('searchString', searchString)"/>
    </v-row>
</template>

<script>
export default {
    name: "BaseSearch",
  props: {
    nothingToSearch: Boolean
  },
    data: () => ({
        searchString: '',
        hideInput: true
    }),
    methods: {
        clickMagnify() {
            this.searchString = ''
            this.$emit('searchString', '')
            this.hideInput = !this.hideInput
        }
    }
}
</script>

