import ingredients from '../../public/data/ingredients.json'
import preparations from '../../public/data/preparations.json'
import aroma from '../../public/data/aroma.json'
import taste from '../../public/data/taste.json'

export default {
    data: () => ({
        preparations: preparations
    }),
    computed: {
        ingredientList() {
            return ingredients.map(ingredient => (
                {...ingredient, aroma: this.sensationArray(ingredient.aromaDevelopment, aroma),
                    taste: this.sensationArray(ingredient.tasteDevelopment, taste)}
                )
            )
        }
    },
    methods: {
        sensationArray(IngredientSensationDevelopment, sensation) {
            return sensation.map( sensation => (
                !IngredientSensationDevelopment.some( ingredientSensation => ingredientSensation.id === sensation.id)
                    ? 0
                    : IngredientSensationDevelopment.find(ingredientSensation => ingredientSensation.id === sensation.id).value
                        ? IngredientSensationDevelopment.find(ingredientSensation => ingredientSensation.id === sensation.id).value
                        : 1
            ))
        },
        sumFusion(arrayToSum) {
            return arrayToSum.length > 1
                ? arrayToSum.reduce((r, a) => a.map((b, i) => (r[i] || 0) + b), [])
                : arrayToSum[0]
        }
    }
};
