<template>
  <v-container class="pa-0">
    <TopToolbarSelection :backwardRoute="'FlavourMerger'"
                         :fusionHistoryProp="fusionHistoryProp"
                         :theFusionIndex="fusionIndex">
      <template v-slot:additionalNavigation>
        <BaseCancelDialog/>
      </template>
    </TopToolbarSelection>
    <p class="title text-uppercase text-center w-100">
      Zubereitungsart
    </p>
    <p class="subtitle-1 text-uppercase mb-0 font-weight-medium ml-1">
      eine gute Idee
    </p>
    <CardList :itemList="recommendedPreparations"
              :fusionHistoryProp="fusionHistoryProp"
              :editMode="true"
              :fusionIndex="fusionIndex"/>
    <p class="subtitle-1 mb-0 font-weight-medium ml-1">
      BLO NICHT
    </p>
    <CardList :itemList="notRecommendedPreparations"
              :fusionHistoryProp="fusionHistoryProp"
              :editMode="true"
              :fusionIndex="fusionIndex"/>
  </v-container>
</template>

<script>
import TopToolbarSelection from '@/components/TopToolbarSelection'
import CardList from '@/components/CardList.vue'
import BaseCancelDialog from '@/components/BaseCancelDialog'
import ingredientListMixin from '@/mixins/ingredientListMixin'

export default {
  name: "PreparationSelector.vue",
  components: {
    TopToolbarSelection,
    CardList,
    BaseCancelDialog
  },
  mixins: [ingredientListMixin],
  props: {
    fusionIngredients: Array,
    fusionHistoryProp: Array,
    fusionIndex: Number
  },
  data: () => ({
    recommendedPreparations: [],
    notRecommendedPreparations: []
  }),
  mounted() {
    this.sortPreparations()
  },
  methods: {
    addFusions (preparation) {
      let aromaFusion = []
      let tasteFusion = []
      this.fusionIngredients.map(ingredient => {
        if (ingredient.aroma) {
          aromaFusion.push(this.generateFusion(ingredient.aroma, ingredient.aromaDevelopment, preparation))
          tasteFusion.push(this.generateFusion(ingredient.taste, ingredient.tasteDevelopment, preparation))
        } else {
          console.log('empty ingredient')
        }
      })
      return ({
        ...preparation,
        aromaFusion: aromaFusion,
        tasteFusion: tasteFusion,
        aroma: this.sumFusion(aromaFusion),
        taste: this.sumFusion(tasteFusion)
      })
    },
    generateFusion(IngredientSensations, IngredientSensationsDevelopment, preparation) {
      return IngredientSensations.map((sensation, index) => {
        const ingredientSensation = IngredientSensationsDevelopment.find(e => e.id === index)
        preparation.sensationAmount = ingredientSensation
                ? preparation.sensationAmount + 1
                : preparation.sensationAmount
        if (this.isNotOverlapping(ingredientSensation, preparation.temperature[0], preparation.temperature[1])) {
          preparation.sensationDiscrepancy = preparation.sensationDiscrepancy + 1
          return sensation - 1
        } else {
          return sensation
        }
      })
    },
    isNotOverlapping(ingredient, preparationMin, preparationMax) {
      return ingredient
              ? !(ingredient.minTemperature <= preparationMax && ingredient.maxTemperature >= preparationMin)
              : false
    },
    sortPreparations() {
      this.preparations.map(p => {
        p.sensationDiscrepancy = 0
        p.sensationAmount = 0
        return this.addFusions(p)
      }).sort( (a, b ) => a.sensationDiscrepancy / a.sensationAmount - b.sensationDiscrepancy / b.sensationAmount)
              .forEach(e => {
                if ((e.sensationDiscrepancy / e.sensationAmount) < 0.4) {
                  this.recommendedPreparations.push(e)
                } else {
                  this.notRecommendedPreparations.push(e)
                }
              })
    }
  }
}
</script>

