<template>
  <v-row justify="center"
         @click.stop="showAroma = !showAroma"
         class="ma-0 pb-3"
         style="min-height: 47px;">
    <v-icon
        v-for="(e, i) in arrayToShow" :key="i"
        large
        :class="stylesToShow[i]"
    >
      mdi-circle
    </v-icon>
  </v-row>
</template>

<script>
export default {
  name: 'ColorList',
  props: {
    aroma: Array,
    taste: Array,
    sensoryPerception: Boolean
  },
  data: () => ({
    showAroma: true,
    aromaStyles: ['grassy', 'onion-like', 'flowery', 'balsamic',
      'heavy-floral', 'aromatic', 'spicy', 'roasty', 'trigeminus'],
    tasteStyles: ['sweet', 'sour', 'salty', 'bitter', 'umami', 'trigeminus']
  }),
  computed: {
    perceptionToShow () {
      return this.sensoryPerception !== undefined ? this.sensoryPerception : this.showAroma
    },
    arrayToShow () {
      return this.perceptionToShow ? this.aroma : this.taste
    },
    stylesToShow () {
      return this.perceptionToShow
              ? this.getIconSizes(this.aroma, this.aromaStyles)
              : this.getIconSizes(this.taste, this.tasteStyles)
    }
  },
  methods: {
    getIconSizes(sensation, styles) {
      if (this.$vuetify.breakpoint.xsOnly) {
        return styles.map((e, i) => (
                sensation[i] === 3
                        ? e.concat(' xs-big-icon')
                        : sensation[i] === 2
                        ? e.concat(' xs-middle-icon')
                        : sensation[i] === 1
                                ? e.concat(' xs-small-icon')
                                : e.concat(' grey-icon')
        ))
      } else {
        return styles.map((e, i) => (
                sensation[i] === 3
                        ? e.concat(' big-icon')
                        : sensation[i] === 2
                        ? e.concat(' middle-icon')
                        : sensation[i] === 1
                                ? e.concat(' small-icon')
                                : e.concat(' grey-icon')
        ))
      }
    }
  }
}
</script>

<style scoped>
.grey-icon {
  color: rgb(214, 214, 214)!important;
  font-size: 10px!important;
  }
.xs-small-icon {
  font-size: 16px!important;
}
.xs-middle-icon {
  font-size: 26px!important;
}
.xs-big-icon {
  font-size: 36px!important;
}
.small-icon {
  font-size: 20px!important;
}
.middle-icon {
  font-size: 30px!important;
}
.big-icon {
  font-size: 40px!important;
}
</style>
